import React from "react";
import { compose, withStateHandlers } from "recompose";
import playButtonImage from "assets/img/product/business-cash-account/play-button.svg";

import {
  IFrame,
  Wrapper,
  PlaceholderImage,
  CaptionWrapper,
  PlayButton,
} from "./elements";

const Video = ({
  videoLink,
  placeholderImage,
  placeholderImageSrcSet,
  placeholderImageSizes,
  playingVideo,
  toggleVideoPlaying,
  id,
}) => (
  <Wrapper id={id}>
    {playingVideo ? (
      <IFrame
        src={`${videoLink}?rel=0&autoplay=1`}
        frameBorder="0"
        webkitallowfullscreen
        mozallowfullscreen
        allowFullScreen
        allow="autoplay; encrypted-media"
      />
    ) : (
      <React.Fragment>
        <PlaceholderImage
          src={placeholderImage}
          srcSet={placeholderImageSrcSet}
          sizes={placeholderImageSizes}
          onClick={toggleVideoPlaying}
        />
        <CaptionWrapper onClick={toggleVideoPlaying}>
          <PlayButton src={playButtonImage} />
        </CaptionWrapper>
      </React.Fragment>
    )}
  </Wrapper>
);

export default compose(
  withStateHandlers(
    () => ({
      playingVideo: false,
    }),
    {
      toggleVideoPlaying: () => ({ playingVideo }) => ({
        playingVideo: !playingVideo,
      }),
    },
  ),
)(Video);
