import styled from "@emotion/styled";
import { padding } from "polished";

import { gothamFonts } from "styles/fonts";
import colors from "styles/colors";

import { breakPoints } from "components/Grid.tsx";

import arrowSvg from "assets/img/icons/orange/arrow-up.svg";

export const Section = styled("section")({
  ...padding(60, 0),
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  [breakPoints.medium]: {
    ...padding(30, 0),
  },
});

export const Wrapper = styled("div")({
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
});

export const Title = styled("h3")({
  margin: 0,
  ...gothamFonts.h3,
  color: colors.black,
  textAlign: "center",
  paddingBottom: 42,
  [breakPoints.medium]: {
    paddingBottom: 26,
    textAlign: "left",
    width: "100%",
  },
  [breakPoints.small]: {
    ...gothamFonts["body-xl-medium"],
    paddingBottom: 12,
  },
});

export const Body = styled("p")({
  margin: 0,
  ...gothamFonts["body-l"],
  fontWeight: 300,
  color: colors.black,
  textAlign: "center",
  paddingBottom: 42,
  width: "60%",

  [breakPoints.medium]: {
    width: "100%",
    textAlign: "left",
  },

  [breakPoints.small]: {
    ...gothamFonts["body-m"],
    fontWeight: 300,
  },
});

export const SlideshowInner = styled("div")({
  display: "flex",
  alignItems: "center",
});

export const ImageContainer = styled("div")({
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  position: "relative",
});

export const Image = styled("img")(({ active }) => ({
  display: active ? "block" : "none",
  width: "100%",
}));

export const DesktopIndicatorsContainer = styled("div")({
  display: "flex",
  justifyContent: "space-between",
  position: "absolute",
  top: "80%",

  [breakPoints.medium]: {
    display: "none",
  },
});

export const MobileIndicatorsContainer = styled("div")({
  justifyContent: "space-between",
  display: "none",

  [breakPoints.medium]: {
    width: "80vw",
    display: "flex",
  },
});

export const Indicators = styled("ul")({
  listStyle: "none",
  margin: 0,
  ...padding(10, 0),
  display: "flex",
});

export const Indicator = styled("li")(({ selected, mobile }) => {
  let color;

  if (selected) {
    color = colors.primary;
  } else if (mobile) {
    color = colors.lightGray;
  } else {
    color = colors.white;
  }

  return {
    color,
    fontSize: 20,
    lineHeight: "20px",
    height: 20,
    width: 20,
    textAlign: "center",

    "&:hover": {
      cursor: "pointer",
    },
  };
});

export const Arrow = styled("div")(({ direction, disabled, mobile }) => ({
  height: 80,
  width: 80,
  backgroundSize: "26px auto",
  backgroundPosition: "center",
  backgroundImage: `url(${arrowSvg})`,
  backgroundRepeat: "no-repeat",
  filter: disabled && "grayscale(100%) brightness(1.3)",
  transform: direction === "left" ? "rotate(-90deg)" : "rotate(90deg)",

  display: mobile ? "none" : "block",

  "&:hover": {
    cursor: "pointer",
  },

  [breakPoints.medium]: {
    height: 40,
    width: 40,

    display: mobile ? "block" : "none",
  },
}));

export const Caption = styled("p")({});
