import React from "react";

import { compose, withStateHandlers } from "recompose";

import { Row, Column } from "components/Grid.tsx";

import {
  Section,
  Wrapper,
  Title,
  Body,
  SlideshowInner,
  Arrow,
  ImageContainer,
  Image,
  DesktopIndicatorsContainer,
  MobileIndicatorsContainer,
  Indicators,
  Indicator,
  Caption,
} from "./elements";

const _Slideshow = ({
  title,
  body,
  images,
  caption,
  selectedIndex,
  goToIndex,
  incrementIndex,
  decrementIndex,
}) => (
  <Section>
    <Row padding={30} gutterWidth={26} verticalAlignment="stretch">
      <Column span={1} spanMedium={0} />
      <Column span={10} spanMedium={12}>
        <Wrapper>
          <Title>{title}</Title>
          <Body>{body}</Body>
          <SlideshowInner>
            <Arrow direction="left" onClick={decrementIndex} />
            <ImageContainer>
              {images.map(
                ({ key, defaultSrc, mobile, mobileRetina, desktop }, index) => (
                  <Image
                    key={key}
                    active={index === selectedIndex}
                    src={defaultSrc}
                    srcSet={`
                      ${mobile.path} ${mobile.width}w,
                      ${mobileRetina.path} ${mobileRetina.width}w,
                      ${desktop.path} ${desktop.width}w,
                    `}
                    sizes={`
                      (max-width: 1680px) 100vw,
                      1680px,
                    `}
                  />
                ),
              )}
              <DesktopIndicatorsContainer>
                <Indicators>
                  {images.map((imageProps, index) => (
                    <Indicator
                      key={`indicator-${imageProps.key}`}
                      selected={index === selectedIndex}
                      onClick={() => goToIndex(index)}
                    >
                      {"\u2022"}
                    </Indicator>
                  ))}
                </Indicators>
              </DesktopIndicatorsContainer>
            </ImageContainer>
            <Arrow direction="right" onClick={incrementIndex} />
          </SlideshowInner>
          <MobileIndicatorsContainer>
            <Arrow direction="left" onClick={decrementIndex} mobile />
            <Indicators mobile>
              {images.map((imageProps, index) => (
                <Indicator
                  key={`indicator-${imageProps.key}`}
                  selected={index === selectedIndex}
                  onClick={() => goToIndex(index)}
                  mobile
                >
                  {"\u2022"}
                </Indicator>
              ))}
            </Indicators>
            <Arrow direction="right" onClick={incrementIndex} mobile />
          </MobileIndicatorsContainer>
          <Caption>{caption}</Caption>
        </Wrapper>
      </Column>
      <Column span={1} spanMedium={0} />
    </Row>
  </Section>
);

const _stateHandlers = [
  {
    selectedIndex: 1,
  },
  {
    goToIndex: () => (index) => ({ selectedIndex: index }),
    incrementIndex: ({ selectedIndex }, { images }) => () => ({
      selectedIndex: (selectedIndex + 1) % images.length,
    }),
    decrementIndex: ({ selectedIndex }, { images }) => () => ({
      selectedIndex: (selectedIndex + images.length - 1) % images.length,
      // mod doesn't handle negative numbers properly in JS
    }),
  },
];

const Slideshow = compose(withStateHandlers(..._stateHandlers))(_Slideshow);

export default Slideshow;
